import '@/plugins/axios'
//确定订单
/**输入：orderedGoods:[
 { 
   goodsId:1,
   num:”1”    
}
  ] 
  输出：tatusOrWaterNumId
  */
export function checkOrder(data){
    
  let orderedGoods={"orderedGoods":data}
  // const url = 'http://192.168.43.102:8080/CustomerChoosedMenu/checkOrder';
  const url = 'http://localhost:8080/CustomerChoosedMenu/checkOrder';
    return axios.post(url,orderedGoods)
}
//结账 
/**checkOutData:[
 {
      salesOrderId:1,//收营员ID
   vipId:”1”,//如果没有就是-1
   waterNumId:”1”,//流水单号
sumPrice:”1”,//总价
discountSumPrice:”1”/折后价
}
  ]
 */
export function checkOut(data){
  // const url = 'http://192.168.43.102:8080/customer/checkOut';
  const url = 'http://localhost:8080/customer/checkOut';
  let checkOutData={"checkOutData":data}
    return axios.post(url,checkOutData)
}


