import '@/plugins/axios'

//查询所有桌子  
/**tables:[
    {
        tableId:1,
        status:"1"
      }
    ] */
export function queryTables(){
  console.log("正在获取桌子信息...")
// const url = 'http://192.168.43.102:8080/tables/queryTables';
const url = 'http://localhost:8080/tables/queryTables';
 
  return axios.get(url)
}


//添加桌子
export function addTable(){
  // const url = 'http://192.168.43.102:8080/tables/addTable'; 
  const url = 'http://localhost:8080/tables/addTable'; 
  let data={"option":"1"}
  return axios.post(url,data)
}

//删除桌子 tableId
export function deleteTable(deskid){
  // const url = 'http://192.168.43.102:8080/tables/deleteTable'; 
  const url = 'http://localhost:8080/tables/deleteTable'; 
  let data={"option":0,
            "tableId":deskid}
  return axios.post(url,data)
}



