<template>
<div>
    <div>
        <ul class="cookList">
            <li v-for="desk in deskData" :key="desk.tableId">
                <span class="foodImg"><img :src="deskurl" width="100%"></span>
                <span class="foodName">{{desk.tableId}}号桌</span>
                <el-button size="mini" type="info" v-if="desk.status=='0'" @click="deleteDesk(desk.tableId)">删除</el-button>
                <span class="foodName" v-if="desk.status=='1'" >正在使用</span>
            </li>
        </ul>
        <div class="addDesk">
            <el-button size="mini" type="primary" @click="addDesk()">添加</el-button>
        </div>
    </div>
</div>
</template>

<script>
import {
    queryTables,
    addTable,
    deleteTable
} from '../api/desk'
import {
    checkOrder
} from '../api/order'
import {
    queryGoodsById,
    queryGoodsCategory
} from '../api/goods'
import {
    queryVipById
} from '../api/vip'
export default {
    data() {
        return {
            deskurl: 'https://f10.baidu.com/it/u=860792909,523430724&fm=72',
            deskData: [],
        }
    },
    methods: {
        deleteDesk(deskId) {
            console.log("删除的桌号为" + deskId)
             deleteTable(deskId).then((res) => {
                if (res.data.status == 1) {
                    queryTables().then((res) => {
                        if (res.data) {
                            this.deskData = res.data.tables
                        }
                    })
                }
            })
        },
        addDesk() {

            addTable().then((res) => {
                if (res.data.status == 1) {
                    queryTables().then((res) => {
                        if (res.data) {
                            this.deskData = res.data.tables

                        }
                    })
                }
            })

        }
    },
    created: function () {

        console.log("正在加载商品信息")
        // this.foodtype = this.$store.state.foodtype
        //查询所有桌子
        queryTables().then((res) => {
            if (res.data) {
                this.deskData = res.data.tables

            }
        })

        queryGoodsCategory().then((res) => {
            if (res.data) {
                this.foodtype = res.data.goodsCategory
            }
        })
    },
}
</script>

<style scoped>
.cookList li {
    list-style: none;
    width: 23%;
    border: 1px solid #e5e9f2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
}

.cookList li span {
    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
}

.addDesk {
    float: left;
    margin-top: 50px;
}
</style>



